const { createApp } = require('vue')
const Phase5VendorPortal = require('../views/Phase5VendorPortal.vue').default
const { sharedPinia } = require('../store/shared')
require('../assets/styles.css')

// Extract token from URL path
function extractTokenFromPath() {
  console.log('Current URL:', window.location.href)
  console.log('Current pathname:', window.location.pathname)
  
  const pathParts = window.location.pathname.split('/')
  console.log('Path parts:', pathParts)
  
  // Clean up path parts to remove empty strings
  const cleanParts = pathParts.filter(part => part.length > 0)
  console.log('Clean parts:', cleanParts)
  
  // Look for token in different ways
  let token = null
  
  // Try to find after 'token'
  const tokenIndex = cleanParts.findIndex(part => part === 'token')
  console.log('Token index:', tokenIndex)
  
  if (tokenIndex !== -1 && cleanParts[tokenIndex + 1]) {
    token = cleanParts[tokenIndex + 1]
    console.log('Found token after "token":', token)
  }
  
  // Try to find after 'invitation'
  if (!token) {
    const inviteIndex = cleanParts.findIndex(part => part === 'invitation')
    if (inviteIndex !== -1 && cleanParts[inviteIndex + 1]) {
      token = cleanParts[inviteIndex + 1]
      console.log('Found token after "invitation":', token)
    }
  }
  
  // If still no token, try the last part of the path
  if (!token && cleanParts.length > 0) {
    const lastPart = cleanParts[cleanParts.length - 1]
    if (lastPart && lastPart !== 'vendor-portal') {
      token = lastPart
      console.log('Using last path part as token:', token)
    }
  }
  
  console.log('Final extracted token:', token)
  return token
}

// Get the token
const token = extractTokenFromPath()

// Create Vue app with token prop
const app = createApp(Phase5VendorPortal)

// Set the token prop
app.provide('token', token)

// Add Pinia store
app.use(sharedPinia)

// Mount the app
app.mount('#app')